import React, { useMemo } from "react";

enum TextStyle {
  Main,
  Title,
  SubTitle,
  Header,
  Menu,
  Caption,
  Placeholder,
}
export default function Label({
  title,
  children,
  textAlign = "left",
  color,
  fontWeight,
  padding,
  style,
  className = "",
  type = TextStyle.Main,
}: {
  title: string;
  children?: any;
  textAlign?: string;
  color?: string;
  fontWeight?: string;
  padding?: string;
  style?: any;
  className?: string;
  type?: TextStyle;
}) {
  const styleValue: Object = useMemo(() => {
    let txtStyle: any = { fontSize: 14, color: "#253f60" };
    switch (type) {
      case TextStyle.Header:
        txtStyle["fontSize"] = 17;
        break;
      case TextStyle.Title:
        txtStyle["fontSize"] = 14;
        txtStyle["fontWeight"] = "bold";
        break;
      case TextStyle.SubTitle:
        txtStyle["fontSize"] = 14;
        break;
      case TextStyle.Caption:
        txtStyle["fontSize"] = 12;
        break;
      case TextStyle.Placeholder:
        txtStyle["fontSize"] = 13;
        txtStyle["color"] = "#ccc;";
        break;
    }
    return { ...txtStyle, color, fontWeight, textAlign, padding, ...style };
  }, [color, fontWeight, style]);

  return (
    <span color="inherit" style={styleValue} className={className}>
      {title || children}
    </span>
  );
}
